@value buttonGap: 0.1rem;

.container,
.number,
.team {
  display: grid;
}

.container {
  align-content: center;
  grid-area: keypad;
  min-width: max-content;
}

.number {
  grid-gap: buttonGap;
  grid-template-columns: repeat(3, 1fr);
}

.team {
  column-gap: buttonGap;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: buttonGap;
}
